webpackJsonp([6754400019357],{

/***/ 396:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"runjak.github.io","author":"Jakob Runge"}},"markdownRemark":{"id":"/home/mushu/dev/runjak.github.io/src/pages/2018-06-17-jsconfeu/index.md absPath of file >>> MarkdownRemark","html":"<p>At the beginning of this month I had the privilege to visit <a href=\"https://2018.cssconf.eu/\">CssConfEu</a>\nand <a href=\"https://2018.jsconf.eu/\">JsConfEu</a> for the first time.</p>\n<p>This post doesn’t have a coherent direction and I don’t intend on comming up with one.\nStill I’d like to write down a few misc notes - so let’s try that!</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/arrival-e7bd49bc638942c699be34257c20e463-2e2e8.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Arriving at JsConfEu\"\n        title=\"\"\n        src=\"/static/arrival-e7bd49bc638942c699be34257c20e463-f8fb9.jpg\"\n        srcset=\"/static/arrival-e7bd49bc638942c699be34257c20e463-e8976.jpg 148w,\n/static/arrival-e7bd49bc638942c699be34257c20e463-63df2.jpg 295w,\n/static/arrival-e7bd49bc638942c699be34257c20e463-f8fb9.jpg 590w,\n/static/arrival-e7bd49bc638942c699be34257c20e463-85e3d.jpg 885w,\n/static/arrival-e7bd49bc638942c699be34257c20e463-d1924.jpg 1180w,\n/static/arrival-e7bd49bc638942c699be34257c20e463-2e2e8.jpg 1536w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ul>\n<li>Talks have been recorded and are available on <a href=\"https://www.youtube.com/playlist?list=PL37ZVnwpeshG2YXJkun_lyNTtM-Qb3MKa\">YouTube</a> - go and watch some! They’re really good!</li>\n<li>\n<p>Conference participants and speakers appeared to be a diverse, colorful crowd. Many happy people and a friendly, open atmosphere.</p>\n<ul>\n<li>A friend from work already visited before and this made navigating the conference more fun.</li>\n</ul>\n</li>\n<li>\n<p>This conference is carried by sponsors, which is a contrast to most of the events I’ve visited so far.</p>\n<ul>\n<li>To me this brings with it a stronger focus on work.</li>\n</ul>\n</li>\n</ul>\n<h2>Experiencing the arch</h2>\n<p>So Mozilla had a thing in collaboration with Ian Brill called <a href=\"https://experiencethearch.com/\">the arch</a>. It was beautiful. Here’s why:</p>\n<video style=\"width: 100%\" controls>\n  <source src=\"/colors-314ead887d0a1bcf5b717a10d7dd2b86.mp4\" type=\"video/mp4\" />\n</video>\n<p>It was a tunnel of color lit cells together with an invitation to program an animation for it.</p>\n<table>\n<thead>\n<tr>\n<th align=\"left\">The arch flyer, front</th>\n<th align=\"left\">The arch flyer, back</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"left\">\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/arch1-011ed0a785555ee3c216d03343a46d27-01800.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The arch flyer side 1\"\n        title=\"\"\n        src=\"/static/arch1-011ed0a785555ee3c216d03343a46d27-f8fb9.jpg\"\n        srcset=\"/static/arch1-011ed0a785555ee3c216d03343a46d27-e8976.jpg 148w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-63df2.jpg 295w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-f8fb9.jpg 590w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-85e3d.jpg 885w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-d1924.jpg 1180w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-9452e.jpg 1770w,\n/static/arch1-011ed0a785555ee3c216d03343a46d27-01800.jpg 4032w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </td>\n<td align=\"left\">\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-01800.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The arch flyer side 2\"\n        title=\"\"\n        src=\"/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-f8fb9.jpg\"\n        srcset=\"/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-e8976.jpg 148w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-63df2.jpg 295w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-f8fb9.jpg 590w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-85e3d.jpg 885w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-d1924.jpg 1180w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-9452e.jpg 1770w,\n/static/arch2-789f57f963f7ce9d6e3c86a90799a0a3-01800.jpg 4032w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </td>\n</tr>\n</tbody>\n</table>\n<p>Because I had done <a href=\"https://twitter.com/sicarius/status/901087830381875200\">something similar</a> before\nI had the idea to get a <a href=\"https://www.youtube.com/watch?v=DLzxrzFCyOs\">particular video</a> on the arch.</p>\n<p>First I needed to get hold of the material and get it into shape.\nSo I dug out a few console commands:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token comment\"># Download video:</span>\nyoutube-dl <span class=\"token string\">\"https://www.youtube.com/watch?v=oHg5SJYRHA0\"</span>\n<span class=\"token comment\"># Resize video to desired resolution:</span>\nffmpeg -i RickRoll\\'D-oHg5SJYRHA0.mp4 -vf scale<span class=\"token operator\">=</span>44:36 smallRolled.mp4\n<span class=\"token comment\"># Cut video to desired length:</span>\nffmpeg -i smallRolled.mp4 -t 00:00:30 shortSmallRolled.mp4\n<span class=\"token comment\"># Export video frames with the desired frame rate:</span>\nffmpeg -i shortSmallRolled.mp4 -r 35/1 frames/frame-%03d.bmp</code></pre>\n      </div>\n<p>I gathered the resulting frame images in an array and went ahead to build a script to get the frames into JavaScript.\nA friend found the <a href=\"https://github.com/59naga/pixel-bmp/\">pixel-bmp</a> library which would come in handy in parsing the frame data.\nI later discovered that the uncompressed data led to too big of a script so that I decided to drop every second frame.\nFrom this I came up with the following code:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> frameNames <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'./framenames.json'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">const</span> fileNames <span class=\"token operator\">=</span> frameNames<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span>f <span class=\"token operator\">=></span> <span class=\"token template-string\"><span class=\"token string\">`./frames/</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>f<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> pixelBitmap <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'pixel-bmp'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> fData <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> finish <span class=\"token operator\">=</span> Promise<span class=\"token punctuation\">.</span><span class=\"token function\">all</span><span class=\"token punctuation\">(</span>fileNames<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>fName<span class=\"token punctuation\">,</span> fIndex<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>fIndex <span class=\"token operator\">%</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">===</span> <span class=\"token number\">0</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token keyword\">return</span> pixelBitmap<span class=\"token punctuation\">.</span><span class=\"token function\">parse</span><span class=\"token punctuation\">(</span>fName<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">then</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">[</span>image<span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> frame <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token keyword\">for</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">let</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> image<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span> i<span class=\"token operator\">+=</span><span class=\"token number\">4</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      frame<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n        r<span class=\"token punctuation\">:</span> image<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n        g<span class=\"token punctuation\">:</span> image<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">[</span>i<span class=\"token operator\">+</span><span class=\"token number\">1</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n        b<span class=\"token punctuation\">:</span> image<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">[</span>i<span class=\"token operator\">+</span><span class=\"token number\">2</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    fData<span class=\"token punctuation\">[</span>Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span>fIndex <span class=\"token operator\">/</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> frame<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>I <a href=\"https://twitter.com/sicarius/status/1002902186072924160\">later discovered</a> that the image as read from the frames\nneeded to be transposed to prevent ending up with weird images\nand it took me a moment to get a function to correct for the indices:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">function</span> <span class=\"token function\">frameMod</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">,</span> sliceSize<span class=\"token punctuation\">,</span> total<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> drift <span class=\"token operator\">=</span> i <span class=\"token operator\">*</span> sliceSize<span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">const</span> rem <span class=\"token operator\">=</span> Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span>drift <span class=\"token operator\">/</span> total<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">return</span> Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>i <span class=\"token operator\">*</span> sliceSize <span class=\"token operator\">+</span> rem<span class=\"token punctuation\">)</span> <span class=\"token operator\">%</span> total<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>The thing particularly nice about <code class=\"language-text\">frameMod</code> was that it was easily testable\nbecause I knew the index mapping needed to look like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token punctuation\">[</span> <span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span>\n  <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">,</span>\n  <span class=\"token number\">6</span><span class=\"token punctuation\">,</span> <span class=\"token number\">7</span><span class=\"token punctuation\">,</span> <span class=\"token number\">8</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span>i <span class=\"token operator\">=></span> <span class=\"token function\">frameMod</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">9</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> ≣ <span class=\"token punctuation\">[</span>\n  <span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">6</span><span class=\"token punctuation\">,</span>\n  <span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">7</span><span class=\"token punctuation\">,</span>\n  <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">,</span> <span class=\"token number\">8</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Next I would just print the data to console and pipe that into a file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">finish<span class=\"token punctuation\">.</span><span class=\"token function\">then</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> transposedFrameData <span class=\"token operator\">=</span> fData<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>frame<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> transposedFrame <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">let</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> frame<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">const</span> px <span class=\"token operator\">=</span> frame<span class=\"token punctuation\">[</span><span class=\"token function\">frameMod</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">,</span> <span class=\"token number\">44</span><span class=\"token punctuation\">,</span> frame<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">)</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n      transposedFrame<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span>\n        px<span class=\"token punctuation\">.</span>r<span class=\"token punctuation\">,</span>\n        px<span class=\"token punctuation\">.</span>g<span class=\"token punctuation\">,</span>\n        px<span class=\"token punctuation\">.</span>b<span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token keyword\">return</span> transposedFrame<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`const frameData = </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span><span class=\"token constant\">JSON</span><span class=\"token punctuation\">.</span><span class=\"token function\">stringify</span><span class=\"token punctuation\">(</span>transposedFrameData<span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">;`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Time to stick it together!\nInstead of interpolating between frames I decided to just show every frame twice\nto accomodate for the dropped frames.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> frameData <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'./frameData'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">function</span> <span class=\"token function\">writeFrame</span><span class=\"token punctuation\">(</span>frameIndex<span class=\"token punctuation\">,</span> frame<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> wantedFrame <span class=\"token operator\">=</span> frameData<span class=\"token punctuation\">[</span>Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span>frameIndex <span class=\"token operator\">/</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">||</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">let</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> frame<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    frame<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>wantedFrame<span class=\"token punctuation\">.</span>length <span class=\"token operator\">></span> <span class=\"token number\">0</span><span class=\"token punctuation\">)</span>\n      <span class=\"token operator\">?</span> wantedFrame<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span>\n      <span class=\"token punctuation\">:</span> Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span>Math<span class=\"token punctuation\">.</span><span class=\"token function\">random</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">*</span> <span class=\"token number\">255</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">function</span> <span class=\"token function\">transform</span><span class=\"token punctuation\">(</span>buffer<span class=\"token punctuation\">,</span> rows<span class=\"token punctuation\">,</span> cols<span class=\"token punctuation\">,</span> frameCount<span class=\"token punctuation\">,</span> fps<span class=\"token punctuation\">,</span> isFirst<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> frameSize <span class=\"token operator\">=</span> <span class=\"token number\">3</span> <span class=\"token operator\">*</span> rows <span class=\"token operator\">*</span> cols<span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">let</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> frameCount<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> frame <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Uint8Array</span><span class=\"token punctuation\">(</span>buffer<span class=\"token punctuation\">,</span> frameSize <span class=\"token operator\">*</span> i<span class=\"token punctuation\">,</span> frameSize<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token function\">writeFrame</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">,</span> frame<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">return</span> Promise<span class=\"token punctuation\">.</span><span class=\"token function\">resolve</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n    transform<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Half a day of hunting strange bugs later there was one more conference that’s not a stranger to love.</p>\n<video style=\"width: 100%\" controls>\n  <source src=\"/success-134d94a825458edd974bb2dcec2efe48.mp4\" type=\"video/mp4\" />\n</video>\n<h2>Getting sidetracked with the Y-Combintor</h2>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-c1266.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Picture of JavaScript code for a y-combinator stamped onto a brown paper bag\"\n        title=\"\"\n        src=\"/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-f8fb9.jpg\"\n        srcset=\"/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-e8976.jpg 148w,\n/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-63df2.jpg 295w,\n/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-f8fb9.jpg 590w,\n/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-85e3d.jpg 885w,\n/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-d1924.jpg 1180w,\n/static/y-combinator-759cc65bc1a4137728bd3b57fca94297-c1266.jpg 1200w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nAt the booth of SinnerSchrader they offered the chance to play with stamps and customize a paper bag that one could fill with snacks afterwards.</p>\n<p>I had loads of fun trying to imprint a JavaScript <a href=\"https://en.wikipedia.org/wiki/Fixed-point_combinator\">y-combintor</a> onto a paper bag.</p>\n<p>The source code I was going for should have looked like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Y</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token constant\">F</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>\n  <span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span><span class=\"token function\">x</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>\n  <span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span><span class=\"token constant\">F</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>y<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token function\">x</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>y<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Without the excessive parenthesis you can end up with an example a bit more clearer\nwhich I found on <a href=\"https://blog.klipse.tech/lambda/2016/08/10/pure-y-combinator-javascript.html\">blog.klipse.tech</a>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Y</span> <span class=\"token operator\">=</span> f <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>x <span class=\"token operator\">=></span> <span class=\"token function\">x</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>x <span class=\"token operator\">=></span> <span class=\"token function\">f</span><span class=\"token punctuation\">(</span>y <span class=\"token operator\">=></span> <span class=\"token function\">x</span><span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>y<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Here are some example usages:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> fac <span class=\"token operator\">=</span> <span class=\"token constant\">Y</span><span class=\"token punctuation\">(</span>f <span class=\"token operator\">=></span> n <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>n <span class=\"token operator\">===</span> <span class=\"token number\">0</span> <span class=\"token operator\">?</span> <span class=\"token number\">1</span> <span class=\"token punctuation\">:</span> n <span class=\"token operator\">*</span> <span class=\"token function\">f</span><span class=\"token punctuation\">(</span>n<span class=\"token operator\">-</span><span class=\"token number\">1</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token comment\">// fac(5) -> 120</span>\n<span class=\"token keyword\">const</span> fib <span class=\"token operator\">=</span> <span class=\"token constant\">Y</span><span class=\"token punctuation\">(</span>f <span class=\"token operator\">=></span> n <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>n <span class=\"token operator\">&lt;</span> <span class=\"token number\">2</span> <span class=\"token operator\">?</span> n <span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token function\">f</span><span class=\"token punctuation\">(</span>n <span class=\"token operator\">-</span> <span class=\"token number\">1</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> <span class=\"token function\">f</span><span class=\"token punctuation\">(</span>n <span class=\"token operator\">-</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token comment\">// [0, 1, 2, 3, 4, 5, 6, 7, 8].map(fib) -> [0, 1, 1, 2, 3, 5, 8, 13, 21]</span></code></pre>\n      </div>\n<p>The real beauty behind this is that recursion itself can be abstracted away\nand we could even enhance the calls of <code class=\"language-text\">f</code> to add additional magic.\nIndeed <a href=\"https://blog.klipse.tech/lambda/2016/08/10/y-combinator-app-javascript.html\">blog.klipse.tech</a> also has a nice example of this where they demonstrate how one can use this to add memoization.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-01800.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Exiting JsConfEu\"\n        title=\"\"\n        src=\"/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-f8fb9.jpg\"\n        srcset=\"/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-e8976.jpg 148w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-63df2.jpg 295w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-f8fb9.jpg 590w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-85e3d.jpg 885w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-d1924.jpg 1180w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-9452e.jpg 1770w,\n/static/exit-4057edcb4fa48c8fa7ee02f108a180d0-01800.jpg 4032w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"title":"CssConfEu JsConfEu 2018","date":"2018-06-24"}}},"pathContext":{"slug":"/2018-06-17-jsconfeu/","previous":{"fields":{"slug":"/hello-world/"},"frontmatter":{"title":"Hello World"}},"next":null}}

/***/ })

});


